import React, {
  useState,
} from 'react';
import {
  Grid,
  InputLabel,
  Typography,
  Button,
  Link,
  Checkbox,
  ListItemText,
  List,
} from '@material-ui/core';
import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt, faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import {
  GridHorizontalDivider,
} from 'Components/UI/';
import {
  BasicTextField,
  BasicSelect,
} from 'Components/UI/';
import {
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { SIDE_MODAL_BLOCK_FIELDS } from '../FieldConstants'


interface SideModalBodyProps {
  formFields?: any;
  invalidFields?: any[];
  refreshData?: (data: any, checkInvalidFields?: any, excludeFields?: any) => void;
  classes?: any;
  deleteIcon?: string;
  deactivateIcon?: string;
  delete?: (data: any) => void;
  deactivate?: (data: any) => void;
  dynamicDropdownChoices?: any[];
  handleClose: () => void;
  handleSave?: (formFields?: any, checkInvalidFields?: any, excludeFields?: any) => void;
  formName: string;
  mode: string;
  confirmationModalTitle?: string;
  confirmationModalMessage?: string;
  checkBoxWithDropDownFieldsInitState?: boolean;
  disableSaveButton?: boolean;
}

export const SideModalBody = (props: SideModalBodyProps) => {


  const { formFields,
    classes,
    handleClose,
    invalidFields
  } = props;
  const [checkBoxWithDropDownChecked, setcheckBoxWithDropDownChecked] = useState(props.checkBoxWithDropDownFieldsInitState ? true : false);
  const {
    checkInvalidFields,
    excludeFields,
    includeFields,
    regularTextBoxFields,
    regularDropDownFields,
    largeTextBoxFields,
    dateFields,
    datePairFields,
    checkBoxWithDropDownFields,
  } = SIDE_MODAL_BLOCK_FIELDS[props.formName][props.mode === 'add' ? 'edit' : props.mode]


  const handleFormChange = (data: any) => {
    if (props.refreshData) {
      props.refreshData(data, checkInvalidFields, excludeFields)
    }
  }

  const handleDateChange = (date: any, form_fields: any, field: string) => {
    try {
      if (
        date &&
        date.toDate() instanceof Date &&
        !isNaN(date.toDate().valueOf())
      ) {
        // console.log('picker date', date)
        // var date_from_picker = new Date(date); // Or the date you'd like converted.
        const iso_date = new Date(new Date(date).getTime() - (new Date(date).getTimezoneOffset() * 60000)).toISOString()
        // console.log('ISO', iso_date)
        // const date_iso = new Date(date)
        // console.log('ISO', date_iso)
        handleFormChange(Object.assign(form_fields, { [field]:  iso_date}))

      } else {
        handleFormChange(Object.assign(form_fields, { [field]: '' }))
      }
    } 
    catch {
      handleFormChange(Object.assign(form_fields, { [field]: '' }))
    } 
    finally {
    }
  };

  console.log('form fields', formFields)

  let body;
  if (formFields && props.mode !== undefined && props.mode !== '') {
    if (props.mode === 'view') {
      const viewDetails = Object.keys(formFields).map(field => (
        <div>
          {!Object.keys(excludeFields).includes(field) && Object.keys(includeFields).includes(field) ? (
            <ListItemText key={field}>
              <div key={field + ' Header'} className={classes.viewHeader}>{includeFields[field].toUpperCase()}</div>
              <div key={field + ' Value'} className={classes.viewHeaderValue}>{formFields && formFields[field] && formFields[field] !== '' ? formFields[field] : '-'}</div>
              <br />
            </ListItemText>
          ) : null}
          {!Object.keys(excludeFields).includes(field) && Object.keys(dateFields).includes(field) ? (
            <ListItemText key={field}>
              <div key={field + ' Header'} className={classes.viewHeader}>{dateFields[field].toUpperCase()}</div>
              <div key={field + ' Value'} className={classes.viewHeaderValue}>{formFields && formFields[field] && formFields[field] !== '' ? moment(formFields[field]).format('LL') : '-'}</div>
              <br />
            </ListItemText>
          ) : null}
        </div>
      ));
      body = (<List>{viewDetails}</List>)
    } else {
      body = Object.keys(formFields).map(field => (
        <div style={{ lineHeight: '0' }}>
          {excludeFields !== undefined &&
            !Object.keys(excludeFields).includes(field) &&
            Object.keys(regularTextBoxFields).includes(field) ? (
              <Grid item xs={12}>
                <BasicTextField
                  labelFontSize="14px"
                  id={field}
                  label={regularTextBoxFields[field]}
                  value={formFields && formFields[field] ? formFields[field] : ''}
                  // error={!(formFields && formFields[field] && !invalidFields.includes(field))}
                  error={invalidFields !== undefined && invalidFields.includes(field)}
                  onChange={(e: any) => {
                    handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
                  }}
                />
              </Grid>
            ) : largeTextBoxFields !== undefined &&
              !Object.keys(excludeFields).includes(field) &&
              Object.keys(largeTextBoxFields).includes(field) ? (
                <Grid item xs={12}>
                  <BasicTextField
                    labelFontSize="14px"
                    id={field}
                    rows="3"
                    label={largeTextBoxFields[field]}
                    value={formFields && formFields[field] ? formFields[field] : ''}
                    // error={!(formFields && formFields[field] && !invalidFields.includes(field))}
                    error={invalidFields !== undefined && invalidFields.includes(field)}
                    onChange={(e: any) => {
                      handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
                    }}
                  />
                </Grid>
              ) : datePairFields &&
                !Object.keys(excludeFields).includes(field) &&
                Object.keys(datePairFields).includes(field) &&
                Object.keys(datePairFields).indexOf(field) % 2 === 0 ? (
                  <Grid container>
                    <Grid>
                      <InputLabel htmlFor={field} style={{ fontSize: '14px' }}>
                        {datePairFields[field]}
                      </InputLabel>
                      <KeyboardDatePicker
                        id={field}
                        variant="dialog"
                        format="MM/DD/YYYY"
                        inputVariant="outlined"
                        error={invalidFields !== undefined && invalidFields.includes(field)}
                        className={classes.keyboardDatePicker}
                        value={formFields &&
                          formFields[field] !== '' &&
                          formFields[field] !== undefined &&
                          formFields[field] !== null ?
                          moment(formFields[field]).format('MM-DD-YY').toString() : ''}
                        invalidDateMessage=""
                        onChange={date => {
                          handleDateChange(date, formFields, field)

                        }}
                      />
                    </Grid>

                    <Typography display="inline">
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    </Typography>

                    <Grid>
                      <InputLabel htmlFor={Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]} style={{ fontSize: '14px' }}>
                        {datePairFields[Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]]}
                      </InputLabel>
                      <KeyboardDatePicker
                        id={Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]}
                        variant="dialog"
                        format="MM/DD/YYYY"
                        inputVariant="outlined"
                        error={invalidFields !== undefined &&
                          invalidFields.includes(Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1])}
                        className={classes.keyboardDatePicker}
                        value={formFields &&
                          formFields[Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]] !== '' &&
                          formFields[Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]] !== undefined &&
                          formFields[Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]] !== null ?
                          moment(formFields[Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1]]).format('MM/DD/YY').toString() : ''}
                        invalidDateMessage=""
                        onChange={date => {
                          handleDateChange(date, formFields, Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1])
                        }}
                      />
                    </Grid>
                  </Grid>



                ) : dateFields &&
                  !Object.keys(excludeFields).includes(field) &&
                  Object.keys(dateFields).includes(field) ? (
                    <Grid item xs={12}>
                      <InputLabel htmlFor={field} style={{ fontSize: '14px' }}>{dateFields[field]}</InputLabel>
                      <div style={{ display: 'flex', alignItems: 'center' }}>
                        <KeyboardDatePicker
                          // labelFontSize="14px"
                          id={field}
                          variant="dialog"
                          format="MM/DD/YYYY"
                          inputVariant="outlined"
                          className={classes.keyboardDatePicker}
                          // error={!(formFields && formFields[field] && !invalidFields.includes(field))}
                          error={invalidFields !== undefined && invalidFields.includes(field)}
                          value={formFields && formFields[field] !== '' ? moment(formFields[field]).format('MM-DD-YY').toString() : ''}
                          invalidDateMessage=""
                          onChange={date => {
                            handleDateChange(date, formFields, field)
                            // handleFormChange(Object.assign(formFields,{[field]: e.target.value}))
                          }}
                        />
                      </div>
                    </Grid>
                  ) : checkBoxWithDropDownFields &&
                    !Object.keys(excludeFields).includes(field) &&
                    checkBoxWithDropDownFields.includes(field) &&
                    checkBoxWithDropDownFields.indexOf(field) % 5 === 0 ? (
                      <Grid item xs={12}>
                        <Checkbox style={{ marginTop: '-10px', marginLeft: '-10px', marginBottom: '-1px' }}
                          color="primary"
                          classes={{
                            colorPrimary: classes.checkbox,
                          }}
                          checked={checkBoxWithDropDownChecked}
                          onChange={(event: React.ChangeEvent<HTMLInputElement>, ) => {
                            setcheckBoxWithDropDownChecked(event.target.checked);
                            handleFormChange(Object.assign(formFields, { [checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 2]]: event.target.checked }))
                          }}
                        /> {checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 3]}


                        {checkBoxWithDropDownChecked ? (
                          <React.Fragment>
                            <BasicSelect
                              marginTop='10px'
                              marginBottom='10px'
                              labelFontSize='14px'
                              id={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 1]}
                              label={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 1]}
                              error={	invalidFields !== undefined && invalidFields !== undefined && invalidFields.includes(checkBoxWithDropDownFields[0]) }
                              menuItems={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 4]}
                              value={formFields && formFields[field] ? formFields[field] : '-'}
                              onChange={(e: any) => {
                                handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
                              }}
                              width="230px"
                            />
                          </React.Fragment>
                        ) : null}
                      </Grid>
                    ) : regularDropDownFields !== undefined &&
                      !Object.keys(excludeFields).includes(field) &&
                      Object.keys(regularDropDownFields).includes(field) ? (
                        <React.Fragment>
                          <BasicSelect
                            marginTop='10px'
                            marginBottom='10px'
                            labelFontSize='14px'
                            id={field}
                            label={regularDropDownFields[field]}
                            // error={	invalidFields.includes(checkBoxWithDropDownFields[field]) }
                            menuItems={props.dynamicDropdownChoices ? props.dynamicDropdownChoices : []}
                            value={formFields && formFields[field] ? formFields[field] : '-'}
                            onChange={(e: any) => {
                              handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
                            }}
                            width="100%"
                            maxWidth="100%"
                          />
                        </React.Fragment>
                      ) : null}
        </div>
      ));
    }
  }

  return (
    <div>
      {body}
      {props.mode === 'edit' && props.deleteIcon && props.deleteIcon !== '' ? (
        <Link
          className={classes.deleteButton}
          onClick={() => {
            if (props.delete) {
              props.delete(formFields)
            }
          }} style={{ marginTop: '-10px', marginBottom: '-10px' }}>
          <FontAwesomeIcon icon={faTrashAlt} style={{ marginRight: '10px' }} />
          {props.deleteIcon}
        </Link>
      ) : null}

      {props.mode === 'edit' && props.deactivateIcon && props.deactivateIcon !== '' ? (
        <Link
          className={classes.deleteButton}
          onClick={() => {
            if (props.deactivate) {
              props.deactivate(formFields)
            }
          }} style={{ marginTop: '-10px', marginBottom: '-10px' }}>
          <FontAwesomeIcon icon={faMinusCircle} style={{ marginRight: '3px', textDecoration: 'underlined' }} />
          {props.deactivateIcon}
        </Link>
      ) : null}

      <GridHorizontalDivider height="10px" />
      {props.mode === 'edit' || props.mode === 'add' ? (
        <Grid container spacing={2} justify="center" alignItems="flex-start">
          <Grid item xs={4}>
            <Button
              onClick={handleClose}
              variant="outlined"
              color="default"
              fullWidth
            >
              Cancel
          </Button>
          </Grid>
          <Grid item xs={4}>
            <Button
              onClick={
                () => {
                  if (props.handleSave) {
                    props.handleSave(formFields, checkInvalidFields, excludeFields)
                  }
                }
              }
              variant="contained"
              color="primary"
              fullWidth
              disabled={props.disableSaveButton ? props.disableSaveButton : false}
            >
              Save
          </Button>
          </Grid>
        </Grid>
      ) : null}


    </div>
  );
};

export default SideModalBody;
