import { Utils } from '@hims/core'
export interface requestOption {
    method: string
    params?: object
    data?: object
    body?: any
}

export default class Fetch {
    private access_token : string  = '';

    getAccessToken = () =>{
        return this.access_token
    }

    fetchAccessToken = async () =>{
        await Utils.StorageService('user_data', 'access_token').then(response => {
          this.access_token = response.result 
          this.jsonHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization' : this.access_token ? `Bearer ${this.access_token}` : ''
            })
        })
        .catch(() => this.access_token = '')
    }

    API_URL = process.env.REACT_APP_BASE_URL

    headers = new Headers({
        'Content-Type': 'text/plain'
    })

    jsonHeaders = new Headers({
        'Content-Type': 'application/json',
        'Authorization' : this.access_token ? `Bearer ${this.access_token}` : ''
    })

    call = (requestOption: requestOption, contextPath: string, jsonHeader?: boolean) => {
        
        const url = `${this.API_URL}${contextPath}`

        const request = new Request(url, {
            ...requestOption,
            headers: jsonHeader ? this.jsonHeaders : this.headers
        });
        return fetch(request).then((res: any) => res.json())
    }
}