interface userData {
  username: string
  first_name: string
  middle_name: string
  last_name: string
  email: string
  expiry_date: string
  location: string
  job_title: string
  department: string
  profile_pic: string
  employee_id: number
  main_module: string
  main_role: string
  section: string
  effectivity_date: string
  roles: any[]
  groups: any[]
}

interface rolesList {
  rows: any[],
  options: any[]
}

interface groupList {
  rows: any[]
}

export const AddUserPage: React.FC<IAddUserPage.IProps> = (
  // props: IAddUserPage.IProps,
): JSX.Element => {
  const classes = useStyles();
  // const theme = useTheme();
  // const [personGroup, setPersonGroup] = React.useState([]);
  // const [personRole, setPersonRole] = React.useState([]);
  const [open, setOpen] = React.useState(false);
  const [selectedDate, setSelectedDate] = React.useState(new Date());
  const [selectedDate2, setSelectedDate2] = React.useState(new Date());
  const [primaryRoleAndModule, setPrimaryRoleAndModule] = React.useState({});
  const [selectedGroup, setSelectedGroup] = React.useState({});
  const [selectedOtherRoles, setSelectedOtherRoles] = React.useState({});
  const [rolesFromGroup, setRolesFromGroup] = React.useState([]);
  const [isFetching, setIsFetching] = React.useState(false);
  const [modalProps, setModalProps] = React.useState({
    title: '',
    message: '',
    open: false
  });
  const [userData, setUserData] = React.useState<userData>({
    first_name: "",
    middle_name: "",
    last_name: "",
    groups: [],
    email: "",
    employee_id: 0,
    department: "",
    job_title: "",
    location: "",
    section: "",
    username: "",
    main_module: "",
    main_role: "",
    effectivity_date: "",
    expiry_date: "",
    roles: [],
    profile_pic: ""
  });

  const [rolesList, setRoleList] = React.useState<rolesList>({
    rows: [],
    options: []
  });
  const [groupList, setGroupList] = React.useState<groupList>({
    rows: []
  });


  React.useEffect(() => {
    getAllRoles(50).then(res => {
      setRoleList({
        ...rolesList,
        ["rows"]: primaryRoleChoicesBeautifier(res.body, true),
        ['options']: primaryRoleChoicesBeautifier(res.body, false)
      })
      // console.log(res.body)
    });



    getAllGroups().then(res => {
      let data = {
        body: res.body,
        label: 'name',
      }

      setGroupList({
        ...groupList,
        ["rows"]: choicesBeautifier(data)
      })
    });
  }, [])

  const choicesBeautifier = (data: any) => {
    let newChoices: any = [];

    if (data.body && data.body.length > 0) {
      data.body.map(c => {
        let beauty = {
          value: c,
          label: c[data.label]
        }

        newChoices.push(beauty)
      })

      return newChoices
    }

    return []
  }

  const primaryRoleChoicesBeautifier = (choices: any, isDefault?: boolean) => {
    let newChoices: any = [];

    if (choices && choices.length > 0) {
      choices.map(c => {
        let beauty = {
          value: c,
          label: ''
        }
        if (!isDefault) {
          beauty.label = `${c.name} - ${c.hasOwnProperty('main_module') ? c.main_module : ''}`
        } else {
          beauty.label = c.name
        }
        newChoices.push(beauty)
      })
      return newChoices
    }

    return []
  }

  const handleAll = (event, prop) => {
    setUserData({
      ...userData,
      [prop]: event.target.value
    });
  };

  const handlePrimaries = (selected: any) => {
    setPrimaryRoleAndModule(selected)
    setUserData({
      ...userData,
      ["main_module"]: selected.value.main_module,
      ["main_role"]: selected.value.name,
    });
  };

  const handleAccountPhoto = (event, prop) => {
    let reader = new FileReader();
    reader.readAsDataURL(event[0]);
    reader.onload = () => {
      // console.log(event);
      setUserData({
        ...userData,
        [prop]: (reader.result as string).split(',')[1]
      });
    };
  };

  const handleEffectivityDateChange = (event, prop) => {
    const newDate = event.toISOString()
    setSelectedDate(event);
    setUserData({
      ...userData,
      [prop]: newDate
    });
  };

  const handleExpireDateChange = (event, prop) => {
    const newDate = event.toISOString()
    setSelectedDate2(event);
    setUserData({
      ...userData,
      [prop]: newDate
    });
  };

  const handleGroup = (selected: any) => {
    console.log(selected.value)
    setSelectedGroup(selected)
    setRolesFromGroup(selected.value.roles)
    setUserData({
      ...userData,
      groups: selected.value
    });
  };

  // var roles2: any[] = []
  const handleRoles = (selected) => {
    // roles2.push(event.target.value)
    // console.log(roles2)
    setSelectedOtherRoles(selected)
    let newValue: any = [];

    if (selected && selected.length > 0) {
      selected.map(s => newValue.push(s.value))
    }

    setUserData({
      ...userData,
      roles: newValue,
    });
    // setPersonRole(event.target.value);
  }

  const handleSubmit = () => {
    setOpen(false);
    setIsFetching(true)
    let payload = userData
    if (payload.effectivity_date === '') {
      delete payload.effectivity_date
    }
    if (payload.expiry_date === '') {
      delete payload.expiry_date
    }

    addNewUser(payload).then((resp: any) => {
      console.log(userData);
      setIsFetching(false)
      if (!resp.error) {
        setModalProps({
          ...modalProps,
          open: true,
          title: 'New user account added',
          message: 'New user account have been successfully added.'
        })
      } else {
        setModalProps({
          ...modalProps,
          open: true,
          title: 'Add New User Failed',
          message: 'Something went wrong. New user wasn’t added.'
        })
      }

    }).catch((err) => {
      setIsFetching(false)
      setModalProps({
        ...modalProps,
        open: true,
        title: 'Add New User Failed',
        message: err.message
      })
    })

    console.log(payload);
    console.log(JSON.stringify(payload));
  }



  //modal function
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClose2 = () => {
    setModalProps({
      ...modalProps,
      open: false
    })
  };

  //arrays
  const departments = [
    {
      value: 'Department 1',
      label: 'Department 1',
    },
    {
      value: 'Department 2',
      label: 'Department 2',
    },
    {
      value: 'Department 3',
      label: 'Department 3',
    },
    {
      value: 'Department 4',
      label: 'Department 4',
    },
  ];


  // const ITEM_HEIGHT = 48;
  // const ITEM_PADDING_TOP = 8;
  // const MenuProps = {
  //   PaperProps: {
  //     style: {
  //       maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
  //       width: 250,
  //     },
  //   },
  // };

  // console.log(props);

  return (
    <>
      {isFetching && <UI.LoadingIndicator />}
      <div className={classes.main}>
        <p>USERS / ACCOUNTS / <b>ADD A USER ACCOUNT</b></p>
        <h2>Add a User Account</h2>
        <Grid container>
          <Grid item xs={12} md={12} lg={12}
            style={{ width: "100%", marginBottom: "10px" }}>
            <InputLabel
              className={classes.inputLabel1}
              style={{ width: "100%" }}>
              Upload Account Photo
            </InputLabel>


            {
              userData.profile_pic ? <img src={`data:image/jpeg;base64,${userData.profile_pic}`} className={classes.previewPhoto} /> :
                <Dropzone
                  multiple={false}
                  onDrop={(event) => {
                    // const base64 = getBase64(event)
                    handleAccountPhoto(event, "profile_pic")
                  }}>
                  {({ getRootProps, getInputProps }) => (
                    <section {...getRootProps({ className: `dropzone ${classes.dropzone}` })}>
                      <div >
                        <input {...getInputProps()}
                        />
                        <img src={require('./faFileUpload.svg')}
                          className={classes.imageDropzone}
                        />
                      </div>
                    </section>
                  )

                  }
                </Dropzone>
            }
          </Grid>

          <Grid item xs={4} md={4} lg={4} className={classes.item}>
            <Grid item xs={12} md={12} lg={12} style={{ width: "95%" }}>
              <InputLabel className={classes.inputLabel}>First Name</InputLabel>
              <TextField type="text"
                value={userData.first_name}
                onChange={(event) => handleAll(event, "first_name")}
                className={classes.input}
                fullWidth
                required
                id="outline-firstname"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >Email Address</InputLabel>
              <TextField type="email"
                value={userData.email}
                onChange={(event) => handleAll(event, "email")}
                className={classes.input}
                fullWidth
                required
                id="outline-emailadd"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >Rank / Job Position</InputLabel>
              <TextField type="text"
                value={userData.job_title}
                onChange={(event) => handleAll(event, "job_title")}
                className={classes.input}
                fullWidth
                required
                id="outline-rank"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >User Name</InputLabel>
              <TextField type="text"
                value={userData.username}
                onChange={(event) => handleAll(event, "username")}
                className={classes.input}
                fullWidth
                required
                id="outline-username"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >Primary Role and Module</InputLabel>
              <UI.Dropdown
                placeholder="Select One"
                icon={true}
                isMulti={false}
                closeOnSelect={true}
                value={primaryRoleAndModule}
                onChange={handlePrimaries}
                choices={rolesList.options} />
              {/* <TextField type="text"
                id="outlined-select-primary_role"
                select
                value={`${userData.main_role} - ${userData.main_module}`}
                
                variant="outlined"
                fullWidth
                className={classes.input}
              >
                {rolesList.rows.map(option => (
                  <MenuItem key={option.name} value={`${option.name} - ${option.hasOwnProperty('main_module') ? option.main_module : ''}`}>
                    {`${option.name} - ${option.hasOwnProperty('main_module') ? option.main_module : ''}`}
                  </MenuItem>
                ))}

              </TextField> */}

            </Grid>
          </Grid>

          <Grid item xs={4} md={4} lg={4} className={classes.item}>
            <Grid item xs={12} md={12} lg={12} style={{ width: "95%" }}>
              <InputLabel className={classes.inputLabel} >Middle Name</InputLabel>
              <TextField type="text"
                value={userData.middle_name}
                onChange={(event) => handleAll(event, "middle_name")}
                className={classes.input}
                fullWidth
                required
                id="outline-middlename"
                variant="outlined" />


              <InputLabel className={classes.inputLabel} >Employee ID Number</InputLabel>
              <TextField type="number"
                value={userData.employee_id}
                onChange={(event) => handleAll(event, "employee_id")}
                className={classes.input}
                fullWidth
                required
                id="outline-empid"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >Location / Office</InputLabel>
              <TextField type="text"
                value={userData.location}
                onChange={(event) => handleAll(event, "location")}
                className={classes.input}
                fullWidth
                required
                id="outline-location"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} htmlFor="role_id">Effectivity Date</InputLabel>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <KeyboardDatePicker
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  value={selectedDate}
                  invalidDateMessage=""
                  onChange={(event) => handleEffectivityDateChange(event, "effectivity_date")}
                  style={{ width: "100%" }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
          </Grid>

          <Grid item xs={4} md={4} lg={4} className={classes.item} >
            <Grid item xs={12} md={12} lg={12} style={{ width: "95%" }}>
              <InputLabel className={classes.inputLabel} >Last Name</InputLabel>
              <TextField type="text"
                value={userData.last_name}
                onChange={(event) => handleAll(event, "last_name")}
                className={classes.input}
                fullWidth
                required
                id="outline-lastname"
                variant="outlined" />

              <InputLabel className={classes.inputLabel} >Department</InputLabel>
              <TextField
                id="outlined-select-department"
                select
                value={userData.department}
                onChange={(event) => handleAll(event, "department")}
                variant="outlined"
                fullWidth
                className={classes.input}
              >
                {departments.map(option => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

              <InputLabel className={classes.inputLabel} >Section (optional)</InputLabel>
              <TextField type="text"
                value={userData.section}
                onChange={(event) => handleAll(event, "section")}
                className={classes.input}
                fullWidth
                required
                id="outline-section"
                variant="outlined" />

              <InputLabel
                className={classes.inputLabel}
                htmlFor="role_id">
                Expiry Date (if applicable)
              </InputLabel>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <KeyboardDatePicker
                  variant="dialog"
                  format="MMMM DD, YYYY"
                  inputVariant="outlined"
                  value={selectedDate2}
                  invalidDateMessage=""
                  onChange={(event) => handleExpireDateChange(event, "expiry_date")}
                  style={{ width: "100%" }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
          </Grid>

          <Grid item xs={4} md={4} lg={4}>
            <Grid item xs={12} md={12} lg={12} style={{ width: "98%" }}>
              <InputLabel className={classes.inputLabel} >Group</InputLabel>

              <UI.Dropdown
                placeholder="Select One"
                icon={true}
                isMulti={false}
                closeOnSelect={true}
                onChange={handleGroup}
                value={selectedGroup}
                choices={groupList.rows} />

            </Grid>
          </Grid>

          <Grid item xs={4} md={4} lg={4}></Grid>

          <Grid item xs={12} md={12} lg={12}>
            <InputLabel
              className={classes.inputLabel}
              style={{ marginTop: "20px" }}>
              Roles from User Groups
            </InputLabel>
            <Typography style={{ textDecoration: "underline", fontWeight: 600 }}>
              {
                rolesFromGroup && rolesFromGroup.length > 0 ?
                  rolesFromGroup.map((r: any, i) =>
                    <span>{r.name}{i !== (rolesFromGroup.length - 1) ? ',' : ''} </span>
                  )
                  : 'None'
              }
            </Typography>
          </Grid>

          <Grid item xs={8} md={8} lg={8}>
            <Grid item xs={12} md={12} lg={12} style={{ width: "98%" }}>
              <InputLabel className={classes.inputLabel} >Other Roles</InputLabel>
              <UI.Dropdown
                placeholder="Search Role"
                icon={false}
                isMulti={true}
                closeOnSelect={false}
                onChange={handleRoles}
                value={selectedOtherRoles}
                choices={rolesList.rows} />
              {/* <Select
                id="outlined-other_roles"
                value={personRole}
                onChange={handleRoles}
                input={<Input id="select-multiple-chip" />}
                multiple
                fullWidth
                renderValue={(selected: any) => {
                  const handleRemoveItem = (index) => {
                    let arr = selected;
                    arr.splice(index, 1);
                    selected = arr;
                  }
                  return (
                    <>
                      {selected.map((value, index) => (
                        <div className={classes.selectedItems}>
                          <span className={classes.selectedText}>{value.name}</span>
                          <span onClick={() => handleRemoveItem(index)}>
                            <FontAwesomeIcon icon={faTimesCircle} />
                          </span>

                        </div>
                      ))}
                    </>
                  )
                }}
                MenuProps={MenuProps}

              >
                {rolesList.rows.map(option => (
                  <MenuItem key={option.name} value={option}>
                    {option.name}
                  </MenuItem>
                ))}
              </Select> */}
            </Grid>
          </Grid>

          <Grid item xs={4} md={4} lg={4}></Grid>

          <Grid item xs={12} md={12} lg={12} style={{ marginTop: "50px" }}>
            <FloatingFooter>
              <Grid item xs={12} className={classes.userAlignRight}>
                <Button
                  onClick={handleOpen}
                  className={classes.saveButton}
                  variant="contained" color="primary">
                  Save
                </Button>
              </Grid>
            </FloatingFooter>
            <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              className={classes.modal}
              open={open}
              onClose={handleClose}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 500,
              }}
            >
              <Fade in={open}>
                <div className={classes.paper}>
                  <List>
                    <ListItemText>
                      <Link
                        className={classes.closeButton}
                        onClick={handleClose}
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </Link>
                    </ListItemText>
                  </List>
                  <p id="transition-modal-title"
                    style={{ fontSize: "20px" }}>
                    <b>Confirm New User</b>
                  </p>
                  <p id="transition-modal-description">
                    Are you sure you want to add
                    <b> {userData.first_name}<br /> {`${userData.last_name}'s`} </b>
                    account to the system? This user <br />
                    will receive a notification email about his/her<br />
                    access to the system.</p><br />
                  <Grid container spacing={1}>
                    <Grid item xs={6}>
                      <Button
                        className={classes.cancelButton}
                        variant="outlined"
                        color="secondary"
                        onClick={handleClose}>
                        Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={6}>
                      <Button
                        // onClick={handleOpen2}
                        onClick={handleSubmit}
                        className={classes.saveButton}
                        variant="contained"
                        color="primary">
                        Save
                      </Button>
                    </Grid>
                  </Grid>
                </div>
              </Fade>
            </Modal>
            <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              className={classes.modal}
              open={modalProps.open}
              onClose={handleClose2}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 500,
              }}
            >
              <Fade in={modalProps.open}>
                <div className={classes.paper}>
                  <List>
                    <ListItemText>
                      <Link
                        className={classes.closeButton}
                        onClick={handleClose2}
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </Link>
                    </ListItemText>
                  </List>
                  <p id="transition-modal-title" style={{ fontSize: "20px" }}><b>{modalProps.title}</b></p>
                  <p id="transition-modal-description">
                    {modalProps.message}
                  </p><br />
                  <Grid container spacing={1}>
                    <Grid item xs={12}>
                      <Button onClick={handleClose2} className={classes.saveButton} variant="contained" color="primary" style={{ marginLeft: "70px" }}>Okay</Button>
                    </Grid>
                  </Grid>
                </div>
              </Fade>
            </Modal>
          </Grid>
        </Grid>
      </div>
    </>
  );
};

const mapStateToProps = (state: Store) => state.roles;
const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(AddUserActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
