import React from //  useEffect //  useState,
'react';
import { Grid, List, ListItemText, Link } from '@material-ui/core';
import { GridHorizontalDivider } from 'Components/UI/';

import { faPencilAlt, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { makeStyles } from '@material-ui/core/styles';

interface SideModalBlockProps {
  blockPosition?: string;
  title?: string;
  showBlock?: string; // set to 'none' or 'block' to close or open the side modal block
  classes?: any;
  handleClose: () => void;
  sideModalBlockMode?: string;
  changeMode?: (value: string) => void;
  editIconDescription?: string;
  children?: any
}


const useStyles =  makeStyles(() => ({
  SideModalBlock: (props : SideModalBlockProps) => ({
    position: props.blockPosition && 
              props.blockPosition === 'fixed' ?
               'fixed' : 'absolute',
    right: 0,
    top: 0,
    zIndex: 200,
    backgroundColor: '#f5f7fb',
    paddingLeft: '30px',
    paddingRight: '30px',
    paddingTop: '15px',
    paddingBottom: '30px',
    borderRadius: '2px',
    boxShadow:
      '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)',
  }),
  SideModalBlockContainer: {},
  editButton: {
    textDecoration: 'underline',
    color: '#3AB77D',
    fontSize: '0.875rem',
    fontWeight: 600,
    cursor: 'pointer',
  },
  deleteButton: {
    textDecoration: 'underline',
    color: '#272E4C',
    opacity: 0.6,
    cursor: 'pointer',
  },
  closeButton: {
    color: '#272E4C',
    position: 'absolute',
    right: 0,
    top: 0,
    '&hover':{
      fontSize: '0.875rem',
    }
  },
}));


export const SideModalBlock = (props: SideModalBlockProps) => {
  const classes = useStyles(props);
  const {
    title,
    handleClose,
    sideModalBlockMode,
    changeMode,
    editIconDescription,
    children,
  } = props;

  return (
    <div>
      {/* {loading ? <Loader /> : null} */}
      <div style={{ display: props.showBlock}}>
        <Grid
          className={classes.SideModalBlock}
          item
          xs={6}
          spacing={0}
          justify="space-between"
          alignItems="flex-start"
          style={{ minHeight: 400, minWidth: 400, maxWidth: 510}}
        >
          <Grid item xs={12}>
            <Grid container justify="space-between">
              <Grid item>
                <h2>
                  {/* <GridHorizontalDivider height="5px" /> */}
                  {title && title !== '' ? title : null}
                </h2>
              </Grid>
              <Grid item style={{ textAlign: 'right' }}>
                <Grid container>
                  <List>
                    <ListItemText>
                      <Link
                        className={classes.closeButton}
                        onClick={handleClose}
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </Link>
                    </ListItemText>
                    {/* <GridHorizontalDivider height="5px" /> */}
                    {sideModalBlockMode === 'view' ? (
                      <ListItemText>
                        <Link
                          className={classes.editButton}
                          onClick={() => {
                            changeMode && changeMode('edit');
                          }}
                        >
                          <FontAwesomeIcon icon={faPencilAlt} />{' '}
                          {editIconDescription}
                        </Link>
                      </ListItemText>
                    ) : null}
                  </List>
                </Grid>
              </Grid>
            </Grid>
          </Grid>

          <GridHorizontalDivider height="5px" />

          {/* BODY OF THE BLOCK IN VIEW, EDIT, OR ADD MODE */}
          <Grid item xs={12}>
            { children }
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default SideModalBlock;
