import React from 'react';

import Select from 'react-select';

interface IProps {
    choices: any[]
    onChange: (value) => void
    value: any
    isMulti: boolean
    closeOnSelect: boolean
    icon: boolean,
    placeholder: string
}

const multiValueStyles = {
    multiValue: (styles) => {
        return {
            ...styles,
            borderRadius: '25px'
        };
    }
};


export const Dropdown: React.FC<IProps> = (props: IProps):JSX.Element => {
    const {
        choices,
        onChange,
        value,
        isMulti,
        closeOnSelect,
        icon,
        placeholder
    } = props;

    return (
        <Select
          className="basic-single"
          classNamePrefix="select"
          value={value instanceof Object && Object.entries(value).length <= 0 ? '' : value}
          isSearchable={true}
          name="color"
          options={choices}
          onChange={onChange}
          placeholder={placeholder}
          isMulti={isMulti}
          closeMenuOnSelect={closeOnSelect}
          components={!icon ? { DropdownIndicator:() => null, IndicatorSeparator:() => null } : {}}
          styles={multiValueStyles}
        />
    )
}